.libros-container {
  max-width: 700px;
  margin: 2rem auto;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  padding: 2rem 2.5rem;
  font-family: 'Segoe UI', Arial, sans-serif;
}
h1 {
  text-align: center;
  color: #2d3a4a;
  margin-bottom: 2rem;
}
.testamento {
  margin-bottom: 2rem;
}
h2 {
  color: #1976d2;
  border-bottom: 2px solid #e3e8ee;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
}
ul {
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  gap: 2rem;
  list-style: decimal inside;
  padding: 0;
}
li {
  padding: 0.3rem 0;
  color: #374151;
  font-size: 1.08rem;
  border-radius: 4px;
  transition: background 0.2s;
}
li:hover {
  background: #f0f4fa;
}
